export interface IMenus {
  icon: string;
  content: string;
  path: string;
}

const guestUserMenu = [
  { icon: "home", content: " ", path: "/" },
  { icon: "cab", content: "", path: "/rental" }
];

const nomalUserMenu = [...guestUserMenu, { icon: "calculator", content: "", path: "/estimate/list" }];

const capitalUserMenu = [...nomalUserMenu];

const superAdminMenu = [...capitalUserMenu, { icon: "television", content: "", path: "/admin/edit" }];

export const getMenus = (level: number) => {
  const isGuestUser = level === 0;
  if (isGuestUser) {
    return guestUserMenu;
  }

  const isNomalUser = level < 2;
  if (isNomalUser) {
    return nomalUserMenu;
  }

  const isCapitalUser = level === 5;
  if (isCapitalUser) {
    return capitalUserMenu;
  }

  const isSuperAdmin = level === 10;
  if (isSuperAdmin) {
    return superAdminMenu;
  }

  return [];
};
