import "./Modal.css";

import React, { Component } from "react";

import { ABOVE21, ABOVE26 } from "../Rental";
import { IRentalStates } from "../IRental";

interface IModalProps {
  rentalData: IRentalStates;
  detailClicked: boolean;
  handleSave: () => void;
}

export default class Modal extends Component<IModalProps> {
  constructor(props: IModalProps) {
    super(props);
  }

  shouldComponentUpdate(nextProps: IModalProps) {
    return nextProps.detailClicked !== this.props.detailClicked;
  }

  render() {
    const { brand, model, detail, grade, option } = this.props.rentalData.currentItem.toObject();
    const { totalPrice } = this.props.rentalData.currentPrice.toObject();
    const { capital, profit } = this.props.rentalData.currentCapital.toObject();
    const { rentalPeriod, insurancePlan, deposit, advancePay } = this.props.rentalData.currentTerms.toObject();

    const insurancePrice = insurancePlan === "21 " ? ABOVE21 : ABOVE26;

    const finalRent = totalPrice * (1 + profit / 100) + insurancePrice;
    const depositPercentage = deposit * 100;
    const depositPrice = finalRent * deposit;
    const advancePayPercentage = advancePay * 100;
    const advancePayPrice = finalRent * advancePay;
    const initialPrice = depositPrice + advancePayPrice;
    const monthlyRent = (finalRent - initialPrice) / rentalPeriod;

    return (
      <div className="capital-modal">
        <h2>    </h2>
        <div className="capital-table-container">
          <div className="capital-table-form">
            <div className="capital-table-title">
              <i className="fa fa-check-square-o" />
              
            </div>
            <div className="capital-table">
              <div className="capital-table-head">
                <div></div>
                <div></div>
                <div></div>
              </div>
              <div className="capital-table-content">
                <div>{`${brand} ${model}`}</div>
                <div>{`${detail} ${grade}`}</div>
                <div>{option}</div>
              </div>
            </div>
          </div>
          <div className="capital-table-form">
            <div className="capital-table-title">
              <i className="fa fa-check-square-o" />
              
            </div>
            <div className="capital-table">
              <div className="capital-table-head">
                <div></div>
                <div></div>
                <div></div>
                <div></div>
              </div>
              <div className="capital-table-content">
                <div>{capital}</div>
                <div>{`${rentalPeriod}`}</div>
                <div>{insurancePlan}</div>
                <div>{`${finalRent.toLocaleString()}`}</div>
              </div>
            </div>
          </div>
          <div className="capital-table-form">
            <div className="capital-table-title">
              <i className="fa fa-calculator" />
               
            </div>
            <div className="capital-table">
              <div className="capital-table-head">
                <div> (1)</div>
                <div> (2)</div>
                <div> (1+2)</div>
                <div> </div>
              </div>
              <div className="capital-table-content">
                <div>{`(${depositPercentage}%) ${Math.floor(depositPrice).toLocaleString()}`}</div>
                <div>{`(${advancePayPercentage}%) ${Math.floor(advancePayPrice).toLocaleString()}`}</div>
                <div>{`${Math.floor(initialPrice).toLocaleString()}`}</div>
                <div>{`${Math.floor(monthlyRent).toLocaleString()}`}</div>
              </div>
            </div>
          </div>
          <div className="btn-container">
            <input type="button" value=" " onClick={this.props.handleSave} />
          </div>
        </div>
      </div>
    );
  }
}
